<template>
    <div id="app">
        <router-view/>
    </div>
</template>

<script>
   import { mapGetters } from 'vuex';
    export default {
        name: 'App',
        computed: {
            //mapGetters()  Vuex  Vuex  getter 
            //...  JavaScript 
            ...mapGetters([
                'githubUsername',
                'htmlTitle'
            ])
        },
        created() {
            this.$store.dispatch("Init")
            this.$store.dispatch("GetInfo")
            this.$setTitle(this.$route.meta.title)
            let windowSize = this.$util.getWindowSize()    
            console.log(` ${window.location.href}`);
            console.log(` ${this.$route.path}`);
            let pathArr = this.$route.path.split("/")
            //console.log(` ${windowSize.height}   ${windowSize.width} `);
            if (pathArr[1] == "user" && windowSize.height > windowSize.width * 1.2) {
                this.$router.push("/mobile/user/blog")
            }
            if (pathArr[1] == "mobile" && windowSize.height <= windowSize.width * 1.2) {
                this.$router.push("/")
            }
        }
    }
</script>


