//
//Vue.js 
import Vue from 'vue'
//
import App from './App'
import util from './utils/util'
//
import router from './router'
//Markdown 
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import 'mavon-editor/dist/markdown/github-markdown.min.css'
//Vuex  : import store from './store'  store  index.js 
import store from './store'
//UI 
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
// UI 
import Vant from 'vant'
import 'vant/lib/index.css'

//
Vue.use(Vant)
Vue.use(ElementUI)
Vue.use(mavonEditor)

//$

// mavonEditor  markdownIt  Markdown 
const markdownIt = mavonEditor.mavonEditor.getMarkdownIt()
Vue.prototype.$markdown = function (value) {
    //console.log('value: '+ value)
    //console.log('markdownIt:', markdownIt);
    return markdownIt.render(value)
}

//: window.location.reload()
// Vue 
Vue.prototype.$reload = function (context) {
    let NewPage = '/empty'
    context.$router.push(NewPage)  // 
    context.$nextTick(() => (context.$router.go(-1))) // 
}

// store 
Vue.prototype.$setTitle = function (title) {
    if (title) {
        document.title = store.state.configuration.htmlTitle + " - " + title
    } else {
        document.title = store.state.configuration.htmlTitle
    }
}

// util.copy  UI 
Vue.prototype.$share = function (message) {
    if (!message) {
        message = window.location
    } else {
        let arr = (window.location + "").split("#")
        message = arr[0] + "#" + message
    }
    if (util.copy(message)) {
        Vue.prototype.$confirm(',!!', '', {
            showCancelButton: false,
            showClose: false,
            type: 'success'
        })
    } else {
        Vue.prototype.$confirm(',', '', {
            showCancelButton: false,
            showClose: false,
            type: 'warning'
        })
    }
}

Vue.prototype.$mobileShare = function (message) {
    if (!message) {
        message = window.location
    } else {
        let arr = (window.location + "").split("#")
        message = arr[0] + "#" + message
    }
    if (util.copy(message)) {
        Vue.prototype.$dialog.alert({
            title: '',
            message: ',!!'
        })
    } else {
        Vue.prototype.$dialog.alert({
            title: '',
            message: ','
        })
    }
}


Vue.prototype.$util = util

//
Vue.config.productionTip = false

new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>'
})



